.category {
    &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: $gray-opacity;
        padding: 10px;

        &--content {
            display: inline-flex;
            align-items: center;

            h3 {
                margin: 0;
                font-weight: 700;
                color: $black;
                text-transform: uppercase;
            }
        }

        &--tags {
            .tags {
                padding: 0;
                margin: 0;
                display: inline-flex;
                align-items: center;

                &__item {
                    padding: 5px 15px;
                    background-color: $white;
                    border: 0;
                    border-radius: 15px;
                    box-shadow: 0 0 1px rgba(0, 0, 0, 0.08);
                    margin-right: 10px;

                    &:last-child {
                        margin: 0;
                    }

                    &__href {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        text-decoration: none;
                        cursor: pointer;

                        span {
                            display: block;
                            line-height: 1.5;
                            color: $c666;
                        }
                    }
                }
            }
        }
    }
}

.category__silde {
    &__item {
        cursor: pointer;
        padding: 10px;
        background-color: $white;
        border: solid 1px $white;
        transition: all linear .3s;

        &__image {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 15px 0;
            position: relative;

            a {
                display: flex;
                align-items: center;

                img {
                    transition: .3s;
                }
            }

            &--sticker {
                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;

                .percent {
                    background-image: url("../images/discount.png");
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 39px;
                    height: 35px;
                    overflow: hidden;
                    font-weight: 700;
                    color: $white;
                    position: absolute;
                    top: -25px;
                    left: 0;
                    padding: 0 0 5px;
                    background-repeat: no-repeat;
                    background-size: 100% 100%;
                    background-position: center;
                }

                .flashsale {
                    background-image: url("../images/fire.png");
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 40px;
                    height: 50px;
                    overflow: hidden;
                    font-weight: 700;
                    color: $white;
                    line-height: 1.2;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    background-repeat: no-repeat;
                    background-size: 100% 100%;
                    background-position: center;
                    text-transform: uppercase;
                    font-size: 8px;
                    padding: 20px 0 0;
                    text-align: center;
                }
            }
        }

        &__box {
            &--name {
                margin-bottom: 10px;
                display: block;

                h3 {
                    color: $black;
                    font-weight: 700;
                    margin: 0;
                }
            }

            &--price {
                display: inline-flex;
                align-items: center;
                margin-bottom: 10px;

                .price {
                    margin-bottom: 0;
                    font-weight: 700;
                    color: red;
                    margin-right: 5px;
                }

                .strike-price {
                    margin-bottom: 0;
                    color: #716b6b;
                    text-decoration: line-through;
                }
            }

            &--configuration {
                padding: 5px;
                margin-bottom: 10px;
                border-radius: 2px;
                background-color: $gray-opacity;

                ul {
                    margin: 0;
                    padding: 0;
                    display: flex;
                    flex-direction: column;

                    li {
                        font-size: 10px;
                        color: $c707070;
                        line-height: 1.5;
                        display: -webkit-box;
                        -webkit-line-clamp: 1;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    }
                }
            }

            &--promotion {
                padding: 5px;
                margin-bottom: 10px;
                border-radius: 2px;
                background-color: $gray-opacity;

                .gift-cont {
                    margin-bottom: 0;
                    line-height: 1.4;
                    color: $text-color;
                    text-align: left;
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }
            }

            &--rate {
                display: inline-flex;
                align-items: center;

                svg, i {
                    font-size: 12px;
                    color: $text-color;
                }

                svg.checked, i.checked {
                    color: orange;
                }

                .text {
                    margin: 2px 0 0 5px;
                }
            }
        }

        &:hover {
            border-radius: 2px;
            border-color: $border;
            box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
            transition: all linear .15s;

            .category__silde__item__image {
                a {
                    img {
                        transform: scale(1.04);
                        transition: .3s;
                    }
                }
            }
        }
    }

    &__button {
        width: 30px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
        box-shadow: 0 0 4px 0 rgba(0, 0, 0, .2);
        color: #707070;
        top: 45%;
        transition: .3s;
        outline: none;
        opacity: .9;

        &:after {
            font-size: 12px;
            font-weight: bold;
        }
    }

    .swiper-button-next {
        padding-left: 7px;
        border-radius: 100px 0 0 100px;
        right: 0;
    }

    .swiper-button-prev {
        padding-right: 7px;
        border-radius: 0 100px 100px 0;
        left: 0;
    }

    .swiper-button-disabled {
        opacity: 0;
    }
}
